body {
  background: #1f2833;
}
.display-time {
    font-size: 1.3rem;
    width: 3.6rem;
    text-align: center;
}

.display-label {
    display: flex;
    font-size: 0.8rem;
}

.display-label div {
    width: 3.6rem;
}

.display-row {
    display: flex;
}
.display-distance {
    font-size: 1.5rem;
    width: 7rem;
}
.row {
    padding: 1rem 0;
}

.board {
    max-width: 25rem;
    margin-right: auto;
    margin-left: auto;
    font-size: 1.3rem;
    color: #c5c6c7;

}

.display-time {
    display: flex;
    align-items: center;
}

.label {
    text-align: left;
    display: flex;
    align-items: flex-end;
}
.header {
    background-color: #0b0c10;
    /* color: #66fcf1; */
    color: #45a29e;
    height: 20vh;
    
}

.title {
    align-items: center;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 3rem;
}
